.EXPERIENCE {
  background-color: transparent !important;
  height: auto;
  position: relative;
  width: 100%;
  max-width: 1000px;
  margin: 0 auto;
}

.EXPERIENCE .EXCO {
  height: auto;
  position: relative;
  width: 100%;
  max-width: 800px;
  margin: 0 auto 60px auto;
  padding: 20px;
  z-index: 15;
  background-color: transparent !important;
}

.EXPERIENCE .overlap-group {
  height: auto;
  position: relative;
  width: 100%;
  margin-bottom: 20px;
  background-color: transparent !important;
}

.EXPERIENCE .text-wrapper {
  color: var(--textsecondary-text);
  font-family: var(--text-body-text-font-family);
  font-size: var(--text-body-text-font-size);
  font-style: var(--text-body-text-font-style);
  font-weight: var(--text-body-text-font-weight);
  letter-spacing: var(--text-body-text-letter-spacing);
  line-height: var(--text-body-text-line-height);
  margin-top: 10px;
  display: block;
  white-space: normal;
}

.EXPERIENCE .div {
  color: var(--mainprimary);
  font-family: var(--text-heading-3-font-family);
  font-size: var(--text-heading-3-font-size);
  font-style: var(--text-heading-3-font-style);
  font-weight: var(--text-heading-3-font-weight);
  letter-spacing: var(--text-heading-3-letter-spacing);
  line-height: var(--text-heading-3-line-height);
  position: relative;
  text-align: left;
  white-space: normal;
  z-index: 20;
  margin-bottom: 10px;
}

.EXPERIENCE .p {
  color: var(--textprimary-text);
  font-family: var(--text-body-text-font-family);
  font-size: var(--text-body-text-font-size);
  font-style: var(--text-body-text-font-style);
  font-weight: var(--text-body-text-font-weight);
  letter-spacing: var(--text-body-text-letter-spacing);
  line-height: var(--text-body-text-line-height);
  position: relative;
  width: 100%;
  z-index: 20;
  background-color: transparent !important;
}

.EXPERIENCE .BB-TECHNOSOL {
  height: auto;
  position: relative;
  width: 100%;
  max-width: 800px;
  margin: 0 auto 60px auto;
  padding: 20px;
}

.EXPERIENCE .i-co-founded-b-b {
  color: var(--textprimary-text);
  font-family: var(--text-body-text-font-family);
  font-size: var(--text-body-text-font-size);
  font-style: var(--text-body-text-font-style);
  font-weight: var(--text-body-text-font-weight);
  letter-spacing: var(--text-body-text-letter-spacing);
  line-height: var(--text-body-text-line-height);
  position: relative;
  text-align: left;
  width: 100%;
  margin-top: 20px;
}

.EXPERIENCE .overlap {
  height: auto;
  position: relative;
  width: 100%;
  margin-bottom: 20px;
}

.EXPERIENCE .b-b-TECHNOSOLUTIONS {
  color: var(--mainprimary);
  font-family: var(--text-heading-3-font-family);
  font-size: var(--text-heading-3-font-size);
  font-style: var(--text-heading-3-font-style);
  font-weight: var(--text-heading-3-font-weight);
  letter-spacing: var(--text-heading-3-letter-spacing);
  line-height: var(--text-heading-3-line-height);
  position: relative;
  text-align: left;
  white-space: normal;
}

.EXPERIENCE .text-wrapper-3 {
  color: var(--textsecondary-text);
  font-family: var(--text-body-text-font-family);
  font-size: var(--text-body-text-font-size);
  font-style: var(--text-body-text-font-style);
  font-weight: var(--text-body-text-font-weight);
  letter-spacing: var(--text-body-text-letter-spacing);
  line-height: var(--text-body-text-line-height);
  position: relative;
  text-align: left;
  white-space: normal;
  margin-top: 10px;
}

.EXPERIENCE .c-f-CROZIER {
  height: auto;
  position: relative;
  width: 100%;
  max-width: 800px;
  margin: 0 auto 60px auto;
  padding: 20px;
}

.EXPERIENCE .overlap-2 {
  height: auto;
  position: relative;
  width: 100%;
  margin-bottom: 20px;
}

.EXPERIENCE .GUELPH-BRADFORD-JAN {
  color: var(--textsecondary-text);
  font-family: var(--text-body-text-font-family);
  font-size: var(--text-body-text-font-size);
  font-style: var(--text-body-text-font-style);
  font-weight: var(--text-body-text-font-weight);
  letter-spacing: var(--text-body-text-letter-spacing);
  line-height: var(--text-body-text-line-height);
  position: relative;
  text-align: left;
  white-space: normal;
  margin-top: 10px;
}

.EXPERIENCE .during-my-time-at {
  color: var(--textprimary-text);
  font-family: var(--text-body-text-font-family);
  font-size: var(--text-body-text-font-size);
  font-style: var(--text-body-text-font-style);
  font-weight: var(--text-body-text-font-weight);
  letter-spacing: var(--text-body-text-letter-spacing);
  line-height: var(--text-body-text-line-height);
  position: relative;
  width: 100%;
  margin-top: 20px;
}

.EXPERIENCE .text-wrapper-4 {
  color: var(--mainprimary-darker);
  font-family: var(--text-heading-1-font-family);
  font-size: var(--text-heading-1-font-size);
  font-style: var(--text-heading-1-font-style);
  font-weight: var(--text-heading-1-font-weight);
  letter-spacing: var(--text-heading-1-letter-spacing);
  line-height: var(--text-heading-1-line-height);
  position: relative;
  text-align: center;
  white-space: normal;
  margin: 20px 0 40px 0;
  width: 100%;
}

/* Responsive styles */
@media screen and (max-width: 768px) {
  .EXPERIENCE {
    padding: 0 10px;
  }
  
  .EXPERIENCE .EXCO,
  .EXPERIENCE .BB-TECHNOSOL,
  .EXPERIENCE .c-f-CROZIER {
    padding: 15px;
    margin-bottom: 40px;
  }
  
  .EXPERIENCE .div,
  .EXPERIENCE .b-b-TECHNOSOLUTIONS {
    font-size: 18px;
    line-height: 24px;
  }
  
  .EXPERIENCE .text-wrapper,
  .EXPERIENCE .text-wrapper-3,
  .EXPERIENCE .GUELPH-BRADFORD-JAN {
    font-size: 14px;
  }
  
  .EXPERIENCE .p,
  .EXPERIENCE .i-co-founded-b-b,
  .EXPERIENCE .during-my-time-at {
    font-size: 14px;
    line-height: 20px;
  }
  
  .EXPERIENCE .text-wrapper-4 {
    font-size: 24px;
    margin: 10px 0 30px 0;
  }
}

@media screen and (max-width: 480px) {
  .EXPERIENCE {
    padding: 0 5px;
  }
  
  .EXPERIENCE .EXCO,
  .EXPERIENCE .BB-TECHNOSOL,
  .EXPERIENCE .c-f-CROZIER {
    padding: 10px;
    margin-bottom: 30px;
  }
  
  .EXPERIENCE .div,
  .EXPERIENCE .b-b-TECHNOSOLUTIONS {
    font-size: 16px;
    line-height: 22px;
  }
  
  .EXPERIENCE .text-wrapper,
  .EXPERIENCE .text-wrapper-3,
  .EXPERIENCE .GUELPH-BRADFORD-JAN {
    font-size: 12px;
  }
  
  .EXPERIENCE .p,
  .EXPERIENCE .i-co-founded-b-b,
  .EXPERIENCE .during-my-time-at {
    font-size: 12px;
    line-height: 18px;
  }
  
  .EXPERIENCE .text-wrapper-4 {
    font-size: 20px;
    margin: 5px 0 20px 0;
  }
}