html, body {
    height: 100%;
    width: 100%;
  }

.RESUME {
  background-color: transparent;
  height: auto;
  position: relative;
  width: 100%;
  max-width: 1000px;
  margin: 40px auto;
  display: flex;
  flex-direction: column;
  align-items: center;
}

.RESUME .techImage {
  height: 180px;
  width: 200px;
  position: relative;
  margin: 20px 0;
  transition: transform 0.3s ease;
  cursor: pointer;
  display: block;
}

.RESUME .downloadImage {
  height: 180px;
  width: 180px;
  position: relative;
  margin: 20px 0;
  transition: transform 0.3s ease, opacity 0.3s ease;
  cursor: pointer;
  display: block;
}

.RESUME .downloadImage:hover,
.RESUME .techImage:hover {
  transform: scale(1.1);
  opacity: 0.8;
}

.RESUME .downloadImage:active,
.RESUME .techImage:active {
  transform: scale(0.95);
}

.RESUME .text-wrapper {
  color: var(--mainprimary);
  font-family: var(--text-heading-1-font-family);
  font-size: var(--text-heading-1-font-size);
  font-style: var(--text-heading-1-font-style);
  font-weight: var(--text-heading-1-font-weight);
  letter-spacing: var(--text-heading-1-letter-spacing);
  line-height: var(--text-heading-1-line-height);
  position: relative;
  text-align: center;
  margin: 20px 0 40px 0;
  white-space: normal;
  width: 100%;
}

.RESUME .resume-container {
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  gap: 40px;
  width: 100%;
  padding: 0 20px;
}

.RESUME .resume-card {
  display: flex;
  flex-direction: column;
  align-items: center;
  width: 100%;
  max-width: 400px;
  margin-bottom: 40px;
}

.RESUME .overlap-group,
.RESUME .overlap {
  height: auto;
  position: relative;
  width: 100%;
  display: flex;
  flex-direction: column;
  align-items: center;
}

.RESUME .TECH-RESUME,
.RESUME .CIVIL-PROJ {
  height: auto;
  position: relative;
  width: 100%;
  display: flex;
  flex-direction: column;
  align-items: center;
  margin-bottom: 20px;
}

.RESUME .div,
.RESUME .text-wrapper-2 {
  color: var(--mainprimary-lighter);
  font-family: var(--text-heading-3-font-family);
  font-size: var(--text-heading-3-font-size);
  font-style: var(--text-heading-3-font-style);
  font-weight: var(--text-heading-3-font-weight);
  letter-spacing: var(--text-heading-3-letter-spacing);
  line-height: var(--text-heading-3-line-height);
  position: relative;
  text-align: center;
  margin-bottom: 15px;
  width: 100%;
}

.RESUME .p {
  color: var(--textprimary-text);
  font-family: var(--text-body-text-font-family);
  font-size: var(--text-body-text-font-size);
  font-style: var(--text-body-text-font-style);
  font-weight: var(--text-body-text-font-weight);
  letter-spacing: var(--text-body-text-letter-spacing);
  line-height: var(--text-body-text-line-height);
  position: relative;
  text-align: center;
  width: 100%;
  margin-top: 20px;
}

/* Responsive styles */
@media screen and (max-width: 768px) {
  .RESUME {
    margin: 30px auto;
  }
  
  .RESUME .text-wrapper {
    font-size: 24px;
    margin: 15px 0 30px 0;
  }
  
  .RESUME .resume-container {
    gap: 20px;
  }
  
  .RESUME .techImage,
  .RESUME .downloadImage {
    height: 150px;
    width: 150px;
    margin: 15px 0;
  }
  
  .RESUME .div,
  .RESUME .text-wrapper-2 {
    font-size: 18px;
  }
  
  .RESUME .p {
    font-size: 14px;
  }
}

@media screen and (max-width: 480px) {
  .RESUME {
    margin: 20px auto;
  }
  
  .RESUME .text-wrapper {
    font-size: 20px;
    margin: 10px 0 20px 0;
  }
  
  .RESUME .resume-container {
    gap: 10px;
    padding: 0 10px;
  }
  
  .RESUME .techImage,
  .RESUME .downloadImage {
    height: 120px;
    width: 120px;
    margin: 10px 0;
  }
  
  .RESUME .div,
  .RESUME .text-wrapper-2 {
    font-size: 16px;
    margin-bottom: 10px;
  }
  
  .RESUME .p {
    font-size: 12px;
    margin-top: 15px;
  }
  
  .RESUME .resume-card {
    margin-bottom: 30px;
  }
}